import React, {Component} from "react";

import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Home from "./Home";
import Trains from "./Trains";
import Tanks from "./Tanks";


class Main extends Component {
  render() {
    return (
      <HashRouter>
        <div>
          <h1>Stuff that is #BrandAllan</h1>
          <ul className="header">
            <li><NavLink to="/brandallan/">Home</NavLink></li>
            <li><NavLink to="/brandallan/trains">Trains</NavLink></li>
            <li><NavLink to="/brandallan/tanks">Tanks</NavLink></li>
          </ul>
          <div className="content">
            <Route path="/brandallan" component={Home}/>
            <Route path="/brandallan/trains" component={Trains}/>
            <Route path="/brandallan/tanks" component={Tanks}/>
          </div>
        </div>
      </HashRouter>
    );
  }
}

export default Main;